<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 提示区域 -->
      <el-alert
        title="添加商品信息"
        type="info"
        show-icon
        center
        :closable="false"
      ></el-alert>
      <!-- 步骤条 -->
      <el-steps :space="200" :active="Number(activeIndex)" finish-status="success" align-center>
        <el-step title="商品内容"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tag标签 -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRule" :tabel-width="100" label-position="top">
        <el-tabs v-model="activeIndex" tab-position="left" :before-leave='beforeTableLeave' @tab-click='taClickAdd'>
          <el-tab-pane label="商品内容" name="0">
            <!--  -->
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>

            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>

            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>

            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!-- 级联选择器 -->
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                  v-model="addForm.goods_cat"
                  :options="catelist"
                  :props="cate_prope"
                  @change="handleChange" clearable></el-cascader>
                </el-form-item>
                <!--  -->
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单的item项 -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableList" :key="item.attr_id">
               <!--  -->
               <el-checkbox-group v-model="item.attr_vals" >
                  <el-checkbox :label="it" v-for="(it,i) in item.attr_vals" :key="i" border	></el-checkbox>
               </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableList" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action表示图片要上传到的后台API地址-->
            <el-upload
              :action="uploadURl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="handleSuccess">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!-- 添加商品的按钮 -->
            <el-button type="primary" class="btnAbb" @click="add">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 图片预览 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="50%" >
      <img :src="previewPath" alt="图片预览" class="previewImg">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data() {
    return {
      activeIndex: '0',
      // 添加商品表单数据、
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 商品所属的分类数组
        goods_cat: [],
        // 图片上传
        pics:[],
        // 商品的详细信息、介绍
        goods_introduce:'',
        attrs:[]
      },
      addFormRef: {},
       
      //商品分类列表
        catelist: [],
        cate_prope: {
          value: 'cat_id',
          label: 'cat_name',
          children: 'children',
          expandTrigger:'hover'
        },
        manyTableList:[],
        onlyTableList:[],
      // 上传图片的URL地址
        uploadURl:'http://127.0.0.1:8888/api/private/v1/upload',
        //图片上传组件的headers请求头对象headerobj: {
        headersObj:{
          Authorization:
          window.sessionStorage.getItem( "token")
        },
        previewPath:'',
        previewVisible:false,
      // 添加表单的验证
      addFormRule: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品', trigger: 'blur' }
        ],
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    //   获取商品分类列表
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品数据失败！')
      }
      this.$message.success('获取商品数据成功！')
      this.catelist = res.data
      // console.log(this.catelist)
    },
    // 级联选择器选中项变化,会触发这个函数
    handleChange() {
      if(this.addForm.goods_cat.length !==3){
        this.addForm.goods_cat = []
      }
    },
    beforeTableLeave(activeName, oldActiveName){
      if(oldActiveName === '0' && this.addForm.goods_cat.length !== 3){
        this.$message.error('请选择商品分类！')
        return false
      }
      return true
    },
    async taClickAdd(){
      // 证明访问的是动态参数面板
      if(this.activeIndex === '1'){
       const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'many'}})
       if(res.meta.status !==200){
         return this.$message.error('获取动态参数列表失败！')
       }
       res.data.forEach(item=>{
        item.attr_vals = item.attr_vals.length === 0? []:item.attr_vals.split(',')
       })
       this.manyTableList = res.data
      }else if(this.activeIndex === '2'){
       const {data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'only'}})
       if(res.meta.status !==200){
         return this.$message.error('获取静态参数列表失败！')
       }
       this.onlyTableList = res.data
      }
    },
    // 处理图片预览效果
    handlePreview(file){
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 处理移除图片效果
    handleRemove(file){
      // 1．获取将要删除的图片的临时路径
      const filePath = file.response.data.tmp_path
      // 2．从pics数组中，找到这个图片对应的索引值
      const i = this.addForm.pics.findIndex(x=>x.pic === filePath)
      //3．调用数组的splice方法，把图片信息对象,从pics数组中移除1
      this.addForm.pics.splice(i,1)
      console.log(this.addForm)
    },
    // 监听图片上传成功的事件
    handleSuccess(response){
      // 1．拼接得到一个图片信息对象const picInfo = { pic:
      const picInfo = {pic:response.data.tmp_path }
      // 2．将图片信息对象，push 到pics数组中
      this.addForm.pics.push(picInfo)
      // console.log(response)
    },
    // 添加商品
    add(){
      this.$refs.addFormRef.validate(async val=>{
        if(!val){
          return this.$message.error('请填写必填项！')
        }
        // 执行添加的逻辑
        // 用lodash的cloneDeep(obj)的功能深拷贝 goods_cat
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')
        // 处理动态参数
        this.manyTableList.forEach(item=>{
          const newMay = {attr_id:item.attr_id,attr_value:item.attr_vals.join(' ')}
          this.addForm.attrs.push(newMay)
        })
        // 处理静态参数
        this.onlyTableList.forEach(item=>{
          const newOny = {attr_id:item.attr_id,attr_value:item.attr_vals}
          this.addForm.attrs.push(newOny)
        })
        form.attrs = this.addForm.attrs
        //发起请求添加商品
        //商品的名称,必须是唯一的
        const {data:res} = await this.$http.post(`goods`,form)
        if(res.meta.status !== 201){
          return this.$message.error(res.meta.msg)
        }
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
        // console.log(res)
      })
    }
    
  },
  computed:{
    cateId(){
      if(this.addForm.goods_cat.length === 3){
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>
<style lang="less" scoped>
.el-steps {
  margin: 10px;
}
.el-checkbox{
  margin: 5px 10px 0 0 !important;
}
.previewImg{
  width: 100%;
}
.btnAbb{
  margin: 10px;
}
</style>